body {
  margin: 0;
}

.athletes {
  grid-template-columns: repeat(2, 200px);
  justify-content: center;
  gap: 20px 15px;
  margin: 30px auto 0;
  display: grid;
}

@media (width >= 1001px) {
  .athletes {
    grid-template-columns: repeat(4, 200px);
  }
}

.athletes__item {
  flex-direction: column;
  align-items: center;
  width: 200px;
  height: 231px;
  transition: transform .3s ease-in-out, box-shadow .3s ease-in-out;
  display: flex;
}

.athletes__item:hover {
  transform: scale(1.2);
  box-shadow: 0 0 10px #0000004d;
}

.athletes__item:hover .person__extra {
  opacity: 1;
  transition: opacity .3s ease-in-out;
}

.person__image-container {
  aspect-ratio: 1;
  width: 200px;
  position: relative;
}

.person__image {
  object-fit: cover;
  border-radius: 50%;
  width: 100%;
  height: 100%;
}

.person__rank {
  color: #8b0000;
  text-align: center;
  background-color: #fff;
  border-radius: 50%;
  width: 32px;
  height: 32px;
  font-size: 16px;
  font-weight: 700;
  line-height: 32px;
  position: absolute;
  top: 50%;
  right: 10px;
  transform: translateY(-50%);
}

.person__details {
  text-align: center;
  margin-top: 8px;
}

.person__name {
  font-size: 20px;
  font-weight: 700;
  line-height: 100%;
  display: block;
}

.person__extra {
  color: #777;
  opacity: 0;
  flex-direction: column;
  gap: 4px;
  font-size: 14px;
  transition: opacity .3s ease-in-out;
  display: flex;
}

.person__sport, .person__pay {
  display: block;
}
